<template>
	<div class="p">
		<div class="left-bg">
			<div class="box">
				

				<img src="../assets/img/title/fx-wzcb.png"/>
				<div class="barHeight">
					<myBar ids="pcFb3" ref="barFb3" :data="barDatas3"></myBar>
				</div>
				<img src="../assets/img/title/fx-wzdwqk.png"/>
				<div class="my-table">
					<div class="table-title">
						<ul>
							<li>
								<label>区域</label>
								<label style="width: 100px;">防汛设备</label>
								<label style="width: 80px;">到位情况</label>
							</li>
						</ul>
					</div>
					<div class="table-content">
						<ul>
							<li v-for="(item,index) in earlyData">
								<label>{{item.date}}</label>
								<label style="width: 100px;">{{item.position}}</label>
								<label style="width: 80px;" :style="{color:item.error?'red':'#fff'}">{{item.content}}</label>

							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import myBar from '@/components/module/echarts/echartBar'
	export default{
		components:{
			myBar
		},
		data(){
			return{
				 barDatas3:{
					title:"",
					data:[72,74,78,86,90,75],
					dataAxis:['A区','B区','C区','D区','E区','G区'],
					unit:''
				},
				earlyData:[{
					date:'天府软件园A区',
					position:'沙袋',
					content:'完备'
				},{
					date:'天府软件园B区',
					position:'沙袋',
					content:'缺失',
					error:true
				},{
					date:'天府软件园C区',
					position:'沙袋',
					content:'完备'
				},{
					date:'天府软件园D区',
					position:'沙袋',
					content:'缺失',
					error:true
				},{
					date:'天府软件园E区',
					position:'沙袋',
					content:'完备'
				},{
					date:'天府软件园G区',
					position:'沙袋',
					content:'完备'
				}]

			}
		}
	}
</script>

<style scoped="scoped">
	.barHeight{
		height: 250px;
	}
	.box{
		color: #fff;
		margin-left: 50px;
		width: 390px;
	}
</style>
<style scoped="scoped" lang="less">
	.my-table{
		display: table;
		width: 100%;
		margin-top: 40px;
		ul{
			li{
				box-sizing: border-box;
				display:table;
				width: 390px;
				color: #FFFFFF;
				list-style: none;
				label,span{
					padding: 0 5px;
					display: table-cell;
				}	
				padding:0;
				margin:0;
			}
			padding:0;
			margin:0;
		}
		.table-title{
			color: #FFFFFF;
			line-height:45px;
			display: table;
			width: 100%;
			background: rgba(0,85,189,.53);
			
		}
		.table-content{
			li{
				padding-bottom: 10px;
				padding-top: 10px;
				border-bottom: 1px solid rgba(255,255,255,.24);
			}
			big{
				font-size: 36px;
				vertical-align: text-bottom;
				line-height: 36px;
			}
			small{
				font-size: 16px;
				vertical-align: text-bottom;
			}
		}
	}
</style>


